import React from "react";
import Title from "../components/Title";
import StoryContent from "../components/StoryContent";
import Element from "../components/Element";
import Section from "../containers/Section";

function description1() {
  return (
    <p className="long-text">
      <strong>Qu’est-ce que c’est ? </strong><br></br> <br></br>Un abonnement mensuel donnant accès à des capsules
      vidéos et des vidéos en directe qui ont pour but de partager des
      enseignements de la voie du coeur. <br></br> <br></br> Par cet abonnement je favorise la guérison et la
      libérations de fréquences qui nous habitent par les experiences des vies
      antérieures et de celle-ci. Je vous invite à connecter aux éléments qui
      nous entourent pour invoquer leur magie cosmique et chamanique par des
      chants, des mouvements, soins et language de lumière comportant tous des
      codes bénéfiques pour la reconfiguration de notre structure énérgétique
      interne et externe. <br></br> <br></br> Je partagerai aussi mon experience qui m’a permis de
      discerner et de libérer les énergies en moi qui m’empêchaient de
      reconnaître ma propre valeur, grandeur et splendeur, pour ainsi déployer
      un chemin de vie unique à moi et magnétiser ce don j’ai besoin pour nourir
      celui-ci. <br></br> <br></br> Un message invoqué de chaque élément est nommé plus bas sur
      cette page.
    </p>
  );
}

function description2() {
  return (
    <p className="long-text">
      Pourquoi j’ai choisis de faire ceci ?<br></br> <br></br> À partir de mon chemin unique,
      jonché d’évolutions et de défis révélateur de ma nature cosmique et
      ancestrale, j’ai dans mon coeur une volonté immense d’incarner l’amour
      inconditionnel ainsi que la guérison du masculin sacré intérne (Que nous
      avons tous et toutes) et externe.<br></br> <br></br> Par cette volonté, j’accepte de tout
      coeur cet appel cosmique à accomplir une grande mission sur cette terre
      qui l’aidera à retrouver ces enfants de coeurs, les créateurs, les
      initiateurs, les guérisseurs, les musiciens et bien plus. Nous avons tous
      un rôle vital en ce monde et j’oeuvre pour accompagner les enfants des
      étoiles à cheminer avec les bons outils et surtout à avancer vers
      l’ouverture du coeur.
    </p>
  );
}

const storyData = [
  {
    name: "subscribe-1",
    side: "default",
    description: description1(),
    img: require("../assets/bg/greece_1.png"),
  },
  {
    name: "subscribe-2",
    side: "reverse",
    description: description2(),
    img: require("../assets/bg/me_3.png"),
  },
];

function airDescription() {
  return (
    <p className="long-text">
      L’élément Eau représente la souplesse et la purification par le mouvement
      continu, de se mouvoir dans les situations en prenant de multiples formes
      pour s’adapter sans se briser, elle apporte la résilience et la force de
      l’ocean inépuisable.
      <br></br>
      <br></br> Ses mouvements sont fluides et souples, en continu et posés.
    </p>
  );
}

function fireDescription() {
  return (
    <p className="long-text">
      L’élément Feu exprime l’explosion et la volonté dans l’action et la
      détermination, il permet l’expression ferme de l’opinion par la voix ainsi
      que par les gestes. Son pouvoir de purification est dans l’intensité et
      permet d’apporter la lumière ainsi que la pureté.
      <br></br>
      <br></br> Ses mouvements sont explosifs et définitifs, secs et fermes.
    </p>
  );
}

function earthDescription() {
  return (
    <p className="long-text">
      L’élément Terre est représentatif de l’ancrage, de la solidité face aux
      intempéries et à la force de la connection aux racines de Gaïa, de son
      énergie de transmutation et de trasmission des sagesses anciennes.
      <br></br>
      <br></br> Ses mouvements sont lent et ancrés, riches et entre l’arrêt et
      le mouvement.
    </p>
  );
}

function waterDescription() {
  return (
    <p className="long-text">
      L’élément Eau représente la souplesse et la purification par le mouvement
      continu, de se mouvoir dans les situations en prenant de multiples formes
      pour s’adapter sans se briser, elle apporte la résilience et la force de
      l’ocean inépuisable.
      <br></br>
      <br></br>Ses mouvements sont fluides et souples, en continu et posés.
    </p>
  );
}

const elementData = [
  {
    img: require("../assets/svg/elements/element_air.svg"),
    description: airDescription()
  },
  {
    img: require("../assets/svg/elements/element_feu.svg"),
    description: fireDescription()
  },
  {
    img: require("../assets/svg/elements/element_terre.svg"),
    description: earthDescription()
  },
  {
    img: require("../assets/svg/elements/element_eau.svg"),
    description: waterDescription()
  },
]

function renderContent(data, index){
  return (
    <div key={index}>
      <StoryContent key={index} {...data} />
      {
        index === 0 ? renderButton() : null
      }
    </div>

  )
}

function renderButton(){
  return (
    <div className="button-container">
      <a href="https://www.patreon.com/satyatavas">
        <button className="button">Abonne toi à mon Patreon</button>
      </a>
    </div>
  )
}

function Subscribe() {
  return (
    <Section id="subscribe-section">
      <div className="container">
        <Title title="Abonnement" />

        {storyData.map((data, index) => renderContent(data, index))}

        <div className="elements-container">
          {elementData.map((data, index) => (
            <div key={index}>
              <Element key={index} {...data} /> 
            </div>
          ))}
        </div>
        
      </div>
    </Section>
  );
}

export default Subscribe;
